import React from 'react'
import Navbar from '../Navbar';
import Footer from '../Footer';
import { CartProvider, useCart} from 'react-use-cart';
import { Col, Container, Row } from 'reactstrap';



function Cart() {
    const {
      isEmpty,
      totalUniqueItems,
      items,
      updateItemQuantity,
      removeItem,
      emptyCart,
      totalItems,
      cartTotal,
    } = useCart();
  
    if (isEmpty) return <h2 className='text-danger text-center mb-4'>Your cart is empty</h2>;
  
    return (
      <div>
        <h1 className='text-center font-weight-bold'>Cart ({totalUniqueItems})
            <button className='btn btn-danger mx-4' onClick={()=>emptyCart()}>Delete All</button>
        </h1>
        
        <Container>
            <Row>
                <Col md="10" className='mb-5 mx-auto'>
                    <table className='table bg-dark text-light'>
                        <thead>
                            <tr>
                                <td>Name</td>
                                <td>Image</td>
                                <td>Quantity</td>
                                <td>Price</td>
                                <td>Operations</td>
                            </tr>
                        </thead>
                        <tbody>
                            {items.map((item) => (
                                    <tr key={item.id}>
                                        <td>
                                            <h6>{item.title}</h6>
                                        </td>
                                        <td>
                                            <img src={item.image_url} className='img-fluid' style={{"width" : '60px', "height" : "60px"}} alt="Error404"/>
                                        </td>
                                        <td>
                                            {item.quantity}
                                        </td>
                                        <td>
                                            {item.quantity*item.price}
                                        </td>
                                        <td>
                                        <button
                                        onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) - 1)} className='btn btn-warning'
                                      >
                                        -
                                      </button>
                                      <button
                                        onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) + 1)} className='btn btn-success mx-3'
                                      >
                                        +
                                      </button>
                                      <button onClick={() => removeItem(item.id)} className='btn btn-danger'>&times;</button>
                                        </td>
                                    </tr>
                            ))}
                            <tr>
                                <td>Total</td>
                                <td>----------</td>
                                <td>
                                    {totalItems}
                                </td>
                                <td>
                                    {cartTotal }
                                </td>
                            </tr>
                        </tbody>
                        
                        
                    </table>
                </Col>
            </Row>
        </Container>
      </div>
    );
  }
  



function AllCarts() {
  return (
    <CartProvider>
        <Navbar/>
        <Cart/>
        <Footer/>
    </CartProvider>
  )
}

export default AllCarts;
