import React from "react";
import { Link } from "react-router-dom";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
function Kids() {
  return (  
    <div>
      <div class="container" id="Kids">
        <div class="row">
          <div class="col-md-12 carousel slide">
            <h2 class="font-weight-bold">Kids Latest</h2>
            <h6 class="text-muted">
              <small>
                Details to details is what makes Hexashop different from the
                other themes.
              </small>
            </h6>
            <OwlCarousel 
            loop 
            navText={["Prev", "Next"]}
            nav={true} 
            items={3}
            className="d-flex flex-row justify-content-around"
            >
              <div className="men-img-1 position-relative ">
                <img
                  src="Images\Kids carousel images\kid-01.jpg"
                  className="img-fluid"
                  alt="Men's 02"
                />
                <h3 className="mt-4">Air Force 1x</h3>
                <h5 className="text-muted">$90.00</h5>
                <div className="men-buttons">
                  <Link to="/Products" className="btn btn-white btn-hover-1 border border-white ml-4">
                    <i className="fa-solid fa-eye" href="/Products"></i>
                    
                  </Link>
                  <Link to="/Products" className="btn btn-white btn-hover-2 border border-white ml-4">
                    <i className="fa-solid fa-star"></i>
                   
                  </Link>
                  <Link to="/Cart" className="btn btn-white btn-hover-3 border border-white ml-4">
                    <i className="fa-solid fa-cart-shopping"></i>
                    
                  </Link>
                </div>
              </div>

              <div className="men-img-2 position-relative mx-5">
                <img
                  src="Images\Kids carousel images\kid-02.jpg"
                  className="img-fluid"
                  alt="Men's 03"
                />
                <h3 className="mt-4">Love Nana' 20</h3>
                <h5 className="text-muted">$150.00</h5>
                <div className="men-buttons">
                  <Link to="/Products" className="btn btn-white btn-hover-2-1 border border-white ml-4">
                    <i className="fa-solid fa-eye"></i>
                    
                  </Link>
                  <Link to="/Products" className="btn btn-white btn-hover-2-2 border border-white ml-4">
                    <i className="fa-solid fa-star"></i>
                    
                  </Link>
                  <Link to="/Cart" className="btn btn-white btn-hover-2-3 border border-white ml-4">
                    <i className="fa-solid fa-cart-shopping"></i>
                    
                  </Link>
                </div>
              </div>

              <div className="men-img-3 position-relative">
                <img
                  src="Images\Kids carousel images\kid-03.jpg"
                  className="img-fluid"
                  alt="Men's 01"
                />
                <h3 className="mt-4">Classic Spring</h3>
                <h5 className="text-muted">$120.00</h5>
                <div className="men-buttons">
                  <Link to="/Products" className="btn btn-white btn-hover-3-1 border border-white ml-4">
                    <i className="fa-solid fa-eye"></i>
                    
                  </Link>
                  <Link to="/Products" className="btn btn-white btn-hover-3-2 border border-white ml-4">
                    <i className="fa-solid fa-star"></i>
                   
                  </Link>
                  <Link to="/Cart" className="btn btn-white btn-hover-3-3 border border-white ml-4">
                    <i className="fa-solid fa-cart-shopping"></i>
                
                  </Link>
                </div>
              </div>
            </OwlCarousel>
          </div>
        </div>
      </div>
      <hr></hr>
    </div>
  );
}

export default Kids;
