import React from 'react'

function MapSection() {
  return (
    <div>
        <div className="container Map">
        <div className="row">
            <div className="col-md-6">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d448.51926446758546!2d-80.12040751041603!3d25.93016394586653!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x88d9ad1877e4a82d%3A0xa891714787d1fb5e!2sNewport%20Fishing%20Pier!5e0!3m2!1sen!2seg!4v1726898166532!5m2!1sen!2seg" width="600" height="450" style={{"border":"0"}} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade" className="iframeMap" title="Newport Fishing Pier
16501 Collins Ave, Sunny Isles Beach, FL 33160, United States"></iframe>
            </div>
            <div className="col-md-6">
                <div className="ml-4">
                    <h2 className="ml-5">Say Hello. Don't Be Shy!</h2>
                    <h6 className="ml-5 text-muted"><small>Details to details is what makes Hexashop different from the other themes.</small></h6>
                </div>
                <div className="form-section mt-5 ml-5">
                    <form action="#" method="post">
                        <div className="d-flex flex-row ">
                          <div className="form-group">
                            <input type="text" className="form-control d-inline-block ml-4 border border-dark w-100 name" id="name" placeholder="Your Name"/>
                          </div>
                          <div className="form-group">
                            <input type="email" className="form-control d-inline-block ml-5 border border-dark w-100 mail" id="Email" placeholder="Your Email Address"/>
                          </div>
                        </div>
                        <div className="form-group">
                          <textarea placeholder="Your message" className="border border-dark ml-4" style={{"width": "100%", "height": "100px"}}></textarea>
                        </div>
                        <button type="submit" className="btn btn-dark ml-4 d-block mt-4 border border-transparent submitButton"><i className="fa-solid fa-paper-plane"></i></button>
                    </form>
                </div>
            </div>
            
        </div>
     </div>
     <hr/>
    </div>
  )
}

export default MapSection;