import React from "react";
import { NavLink } from "react-router-dom";
import { Link } from "react-scroll";
import { CartProvider, useCart } from "react-use-cart";


function Navbar() {
  const{totalItems} = useCart()
  return (
    <CartProvider>
      <div className="container-fluid p-4">
        <div className="row">
          <div className="col-md-6">
            <NavLink to="/" className="hexa">
              <h2>
                HEXASHOP <br />
                <h5>ONLINE SHOPPING</h5>
              </h2>
            </NavLink>
          </div>
          <div className="col-md-6 mr-auto">
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
              <NavLink className="navbar-brand active" to="/">
                Home
              </NavLink>
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarNav"
                aria-controls="navbarNav"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarNav">
                <ul className="navbar-nav">
                  <li className="nav-item active">
                    <Link className="nav-link" to="Mens"
                    smooth={true} 
                    offset={-80} 
                    duration={500} 
                    >
                      Men's <span className="sr-only"></span>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="Women"
                    smooth={true} 
                    offset={-80} 
                    duration={500} 
                    >
                      Women's
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="Kids"
                    smooth={true} 
                    offset={-80} 
                    duration={500} 
                    >
                      Kid's
                    </Link>
                  </li>
                  <li className="nav-item dropdown">
                    <NavLink
                      className="nav-link dropdown-toggle"
                      href="/Home"
                      role="button"
                      data-toggle="dropdown"
                      aria-expanded="false"
                    >
                      Pages
                    </NavLink>
                    <div className="dropdown-menu">
                      <NavLink
                        className="dropdown-item"
                        to="/AboutUs"
                        target="_blank"
                      >
                        About Us
                      </NavLink>
                      <NavLink className="dropdown-item" to="/Products" target="_blank">
                        Products
                      </NavLink>
                      <NavLink
                        className="dropdown-item"
                        to="/SinglePage"
                        target="_blank"
                      >
                        Single Product
                      </NavLink>
                      <NavLink className="dropdown-item" to="/ContactUs" target="_blank">
                        Contact Us
                      </NavLink>
                    </div>
                  </li>
                </ul>
                <NavLink to="/" className="explore">
                  Explore
                </NavLink>
                <NavLink to="/Cart" className="text-dark mx-3">
                <i class="fa-solid fa-cart-plus"></i><h4 className="d-inline-block ml-1">{totalItems}</h4>
                </NavLink>
                
              </div>
            </nav>
          </div>
        </div>
      </div>
      <hr />
    </CartProvider>
  );
}

export default Navbar;
