import React from "react";

function SingleProduct() {
  return (
    <div>
      <div className="container mt-3  productsMargin">
        <div className="row">
          <div className="col-md-8">
            <img
              src="Images/Single product images/single-product-01.jpg"
              alt=""
              className="img-fluid"
            />
            <img
              src="Images/Single product images/single-product-02.jpg"
              alt=""
              className="img-fluid mt-4"
            />
          </div>
          <div className="col-md-4">
            <h3 className="font-weight-bold ml-2">New Green Jacket</h3>
            <h5 className="ml-2 text-muted">$75.00</h5>
            <hr style={{"border": "solid 1px gray"}} />
            <p className="text-muted mt-4">
              Lorem ipsum dolor sit amet, consectetur <br />
              adipiscing elit, sed do eiusmod kon tempor <br />
              incididunt ut labore.
            </p>
            <i className="fa-solid fa-quote-left mr-3"></i>
            <p className="d-inline font-weight-bold">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiuski smod.
            </p>
            <hr style={{"border": "solid 1px gray" }}/>
            <div className="d-flex flex-row justify-content-between mt-5">
              <h3 className="text-muted">No. of Orders</h3>
              <button className="minus-button" onclick="less()" id="toggleButton">
                -
              </button>
              <p id="content" className="mt-2 font-weight-bold"></p>
              <button className="plus-button" onclick="more()">
                +
              </button>
            </div>
            <hr style={{"border": "solid 1px gray"}} className="mt-4" />
            <div className="d-flex flex-row justify-content-between mt-5">
              <h3 className="text-muted">Total: $210.00</h3>
              <button className="btn btn-light border border-dark text-dark card">
                Add To Card
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SingleProduct;
